import { AppRate } from '@ionic-native/app-rate';
import { Component, ViewChild} from '@angular/core';
import { Platform, Nav, ToastController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { TranslateService } from '@ngx-translate/core';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { SafariViewController } from '@ionic-native/safari-view-controller';
import { InAppPurchase2 } from '@ionic-native/in-app-purchase-2';

import { HomePage } from '../pages/home/home';
import { AboutPage } from '../pages/about/about';
import { TermsPage } from '../pages/terms/terms';
import { DeveloperPage } from '../pages/developer/developer';
import { MenuController } from './menu.controller';
import { IAPController } from './iap.controller';

@Component({
  templateUrl: 'app.html'
})
export class MyApp extends MenuController {

  @ViewChild(Nav) nav: Nav;

  rootPage: any = HomePage;

  isPlatformIOS = false;
  isPlatformWeb = false;
  linkIOS: string = 'http://bitflip.com.br/diego-jordao/'
  linkAndroid: string = "https://play.google.com/store/apps/collection/cluster?clp=igNAChkKEzY3NjY5MzY2NDU2MjUzOTM0MjkQCBgDEiEKG2JyLm1lZC5kaWVnb2pvcmRhby50b3JheFBybxABGAMYAQ%3D%3D:S:ANO1ljJ1IDM&gsr=CkOKA0AKGQoTNjc2NjkzNjY0NTYyNTM5MzQyORAIGAMSIQobYnIubWVkLmRpZWdvam9yZGFvLnRvcmF4UHJvEAEYAxgB:S:ANO1ljI3cUA";

  iapController: IAPController;

  homePages: Array<{title: string, icon?: string, component: any}>;
  helperPages: Array<{title: string, icon?: string, component: any}>;
  categoryQuizes: Array<{title: string, name: string, icon?: string, color?:string, component: any}>;
  freeQuizes: Array<{title: string, name: string, icon?: string, color?: string, component: any}>;
  languages: Array<{title: string, file: string, image?: string}>;

  constructor(platform: Platform,
    statusBar: StatusBar,
    splashScreen: SplashScreen,
    private translate: TranslateService,
    private appRate: AppRate,
    private inAppBrowser: InAppBrowser,
    public store: InAppPurchase2,
    private safariViewController: SafariViewController,
    public toastController: ToastController,
    public translateService: TranslateService) {
    super();
    this.store = store;
    this.isPlatformIOS = platform.is('ios');
    this.isPlatformWeb = platform.is('core');

    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      statusBar.hide();
      statusBar.overlaysWebView(false);

      splashScreen.hide();

      // this.setLanguage(translate);
      translate.setDefaultLang('pt');

      console.log('this.platforms', platform.platforms());

      if(platform.is('cordova')){
        this.appRate.preferences.usesUntilPrompt = 5;
        this.appRate.preferences.useLanguage = translate.getDefaultLang();
        this.appRate.preferences.storeAppURL = {
            ios: '1296654435',
            android: 'market://details?id=br.med.diegojordao.idadeOssea'
        };
    
        this.appRate.promptForRating(false);
      }

      console.log('isPlatformWeb', this.isPlatformWeb);
      this.iapController = new IAPController(store, toastController, translateService);
      if (this.store && !this.isPlatformWeb) {
        this.iapController.iapSetup();
      }

      IAPController.userChangedObserver.subscribe((userType)=>{
        console.log('userChangedObserver.subscribe', userType);
        this.initMenu();
      })

    });
      
    this.initMenu();

  }

  initMenu() {
    this.homePages = [
      { title: 'HOME', icon: 'home', component: HomePage },
    ];

    this.helperPages = [
      { title: 'CONTACT', icon: 'md-chatboxes', component: AboutPage },
      { title: 'TERMS', icon: 'md-document', component: TermsPage },
      { title: 'DEVELOPER', icon: 'md-bulb', component: DeveloperPage }
    ];

    this.freeQuizes = this.getFreeCategories();

    this.categoryQuizes = this.getPremiumCategories();

    this.languages = [
      {
        title: 'LANGUAGE_PT',
        file: 'pt',
        image: './assets/icon/icon-pt.png'
      },
      {
        title: 'LANGUAGE_EN',
        file: 'en',
        image: './assets/icon/icon-en.png'
      },
      {
        title: 'LANGUAGE_ES',
        file: 'es',
        image: './assets/icon/icon-es.png'
      }
    ];
  }

  getUserType(): string {
    if (this.iapController) {
      return this.iapController.getUserType();
    }
    return '';
  }

  setLanguage(translate) {
    let language = window.localStorage.getItem('language');
    if(language){
      translate.setDefaultLang(language);
    } else {
      translate.setDefaultLang('pt');
      if (translate.getBrowserLang() !== undefined) {
        translate.use(translate.getBrowserLang());
      } else {
        translate.use('en'); // Set your fallback language here
      }
    }
  }

  getLink() {
    console.log('getLink, isPlatformIOS:', this.isPlatformIOS)
    if (!this.isPlatformIOS) {
      return this.linkAndroid;
    }
    return this.linkIOS;
  }

  redirect() {
    if (this.isPlatformIOS) {
      console.log('redirectIos');
      this.openSafari();
    } else {
      console.log('redirectElse');
      this.openInAppBrowser();
    }
  }

  openInAppBrowser() {
    this.inAppBrowser.create(this.getLink());
  }

  openSafari() {
    this.safariViewController.isAvailable()
      .then((available: boolean) => {
          if (available) {
            this.safariViewController.show({
              url: this.linkIOS,
              hidden: false,
              animated: false,
              transition: 'curl',
              enterReaderModeIfAvailable: true,
              tintColor: '#ff0000'
            })
            .subscribe((result: any) => {
                if(result.event === 'opened') console.log('Opened');
                else if(result.event === 'loaded') console.log('Loaded');
                else if(result.event === 'closed') console.log('Closed');
              },
              (error: any) => console.error(error)
            );

          } else {
            this.openInAppBrowser();
          }
        }
      );
  }

  onChangeLanguage(language) {
    this.translate.use(language);
  }

  openPage(page) {
    this.openPageWithNav(page, this.nav);
  }

}



