import { Component } from '@angular/core';
import { NavController, NavParams, ModalController } from 'ionic-angular';
import { Http } from '@angular/http';
import { TranslateService } from '@ngx-translate/core';

const MAX_TIME = 10;
const NOT_ANSWERED = 'x';

@Component({
  selector: 'page-quiz',
  templateUrl: 'quiz.html',
})
export class QuizPage {

  title: string;
  items = [];
  question: any;
  selectedAnswer: string;
  language: string
  index: number = 0;
  total: number = 0;
  correctAnswers: number = 0;
  wrongAnswers: number = 0;
  notAnswered: number = 0;
  finished: boolean = false;
  timer;
  remainingTime: number = MAX_TIME;
  
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    private http: Http,
    private translate: TranslateService,
    public modalCtrl: ModalController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Quiz');
  }

  ngOnInit() {

      this.title = this.navParams.get('title');
      this.language = this.translate.currentLang;
  
      if (!this.language) {
        this.language = 'pt';
      }

      if (this.language != 'en' && this.language != 'pt' && this.language != 'es') {
        this.language = 'en';
      }
      
      this.setupTimer();
      this.getContent()

      this.translate.onLangChange.subscribe((event) => {
        this.getContent(event.lang)
      })
      
  }

  answerClicked(selectValue: string) {
    this.selectedAnswer = selectValue;
    this.stopTimer();
  }

  isCorrect() {
    return this.selectedAnswer === this.question.answers[0]
  }

  isWrong() {
    if (this.selectedAnswer && this.selectedAnswer !== NOT_ANSWERED) {
      return this.selectedAnswer !== this.question.answers[0]
    }
  }

  isWrongAnswer(value: string) {
    return this.selectedAnswer === value && value !== this.question.answers[0];
  }

  isCorrectAnswer(value: string) {
    return this.selectedAnswer && value === this.question.answers[0];
  }

  isTimeout() {
    return this.selectedAnswer === NOT_ANSWERED;
  }

  isNextOrFinishEnabled (){
    return !this.selectedAnswer;
  }

  setupTimer() {
    if (this.timer) {
      this.stopTimer();
    }
    this.remainingTime = MAX_TIME;
  }

  stopTimer() {
    clearTimeout(this.timer);
  }

  endQuestion() {
    this.selectedAnswer = NOT_ANSWERED;
  }

  startTimer() {
    this.timer = setTimeout(x => 
      {
          if(this.remainingTime > 0) {
            this.remainingTime -= 1;

            if(this.remainingTime>0){
              this.startTimer();
            } else {
              this.endQuestion();
            }
          }

      }, 1000);

  }

  getProgress() {

    if (this.total > 0) {
      const current = (this.index+1);
      return ' - ' + current + '/' + this.total;
    }
    
    return '';

  }

  getContent(lang=this.language) {
    const category = this.navParams.get('name');
    const filename = `quiz-${category}.json`;
    console.log('category', category);
    this.http.get('assets/descriptions/' + lang + '/' + filename).subscribe(data => {
      const content = JSON.parse(data['_body']);
      this.items = content;
      console.log('items', this.items);
      if (this.items[0]) {
        this.question = this.items[0];
        console.log('question', this.question);
      }
      this.total = this.items.length;
      this.startTimer();
    });
  }

  getRemainingTime() {
    if (this.remainingTime < 10) {
      return '0' + this.remainingTime;
    }
    return this.remainingTime;
  }

  nextQuestion() {
    if (this.selectedAnswer == NOT_ANSWERED) {
      ++this.notAnswered;
    } else {
      if(this.items[this.index].answers.indexOf(this.selectedAnswer) !== -1) {
        ++this.correctAnswers;
      } else {
        ++this.wrongAnswers;
      }
    }
    console.log('correctAnswers', this.correctAnswers);
    console.log('wrongAnswers', this.wrongAnswers);
    console.log('notAnswered', this.notAnswered);
    
    this.index++;
    this.question = this.items[this.index];
    this.selectedAnswer = null;
    this.setupTimer();
    this.startTimer();
  }

  finishQuiz() {
    if(this.items[this.index].answers.indexOf(this.selectedAnswer) !== -1) {
      ++this.correctAnswers;
    } else {
      ++this.wrongAnswers;
    }
    this.finished = true;
  }

  resetQuiz() {
    this.selectedAnswer = null;
    this.correctAnswers = 0;
    this.wrongAnswers = 0;
    this.index = 0;
    this.finished = false;
    this.question = this.items[0];
  }
}



