import { Component } from '@angular/core';
import { InAppPurchase2 } from '@ionic-native/in-app-purchase-2';
import { TranslateService } from '@ngx-translate/core';
import { NavController, NavParams, ToastController } from 'ionic-angular';
import { Platform } from 'ionic-angular/platform/platform';
import { IAPController } from '../../app/iap.controller';
import { HomePage } from '../home/home';

@Component({
  selector: 'page-paywall',
  templateUrl: 'paywall.html',
})
export class PaywallPage {

  descriptions: Array<string>
  isPlatformWeb: boolean = false;
  iapController: IAPController;
  
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public platform: Platform,
    public store: InAppPurchase2,
    public toastController: ToastController,
    public translateService: TranslateService
    ) {
    this.store = store;
    this.isPlatformWeb = platform.is('core');
    this.iapController = new IAPController(store, toastController, translateService);
    if (this.store && !this.isPlatformWeb) {
      this.iapController.iapSetup();
    }
  }

  getPrice(): string {
    let price = IAPController.PREMIUM_PRICE;
    if (IAPController.product && IAPController.product.price) {
      price = IAPController.product.price;
    }
    return price.replace('.', ',').replace('R$','R$ ');
  }

  onClickBuy() {

    if (this.store && !this.isPlatformWeb) {
      this.iapController.order();
    } else {
      this.iapController.setUserType(IAPController.USER_TYPE_PREMIUM);
      this.navCtrl.setRoot(HomePage);
    }

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PaywallPage');
  }

  ngOnInit() {
    console.log('ngOnInit PaywallPage');
  }

}



