import { IAPProduct, InAppPurchase2 } from "@ionic-native/in-app-purchase-2";
import { BehaviorSubject } from "rxjs";
import { ToastController } from 'ionic-angular';
import { TranslateService } from "@ngx-translate/core";

export class IAPController {

  static PRODUCT_ID = 'premium';
  static USER_TYPE_FREE = 'free';
  static USER_TYPE_PREMIUM = 'premium';
  static PREMIUM_PRICE = 'R$0.99';

  static userType = IAPController.USER_TYPE_FREE;
  static product: IAPProduct;

  static userChangedObserver = new BehaviorSubject(null);

  constructor(
    public store: InAppPurchase2,
    public toastController: ToastController,
    public translate: TranslateService) {
    this.store = store;
    this.toastController = toastController;
  }

  async setUserType(userType: string): Promise<void> {
    console.log('iapController.setUserType', userType);
    IAPController.userType = userType;
    IAPController.userChangedObserver.next(userType);
    const toast = await this.toastController.create({
      message: this.translate.instant('PURCHASE_SUCCESS'),
      duration: 2000,
      closeButtonText: 'x',
      showCloseButton: true
    });
    toast.present();
  }

  getUserType(): string {
    return IAPController.userType;
  }

  order(): void {
    console.log('product', IAPController.product.price)
    this.store.order(IAPController.PRODUCT_ID);
  }

  iapSetup(): void {
    // this.store.verbosity = this.store.DEBUG;
    this.store.register({
      id: IAPController.PRODUCT_ID,
      alias: IAPController.PRODUCT_ID,
      type: this.store.NON_CONSUMABLE,
    });
    this.store.get(IAPController.PRODUCT_ID);
    this.registerHandlersForPurchase(IAPController.PRODUCT_ID);
    // restore purchase
    this.store.refresh();
  }

  registerHandlersForPurchase(productId): void {
    let self = this.store;
    this.store.when(productId).updated(function (product: IAPProduct) {
      console.log(`updated ${JSON.stringify(product)}`);
      if (product && product.price) {
        IAPController.product = product;
      }
      if(product && product.state === self.OWNED) {
        IAPController.userType = IAPController.USER_TYPE_PREMIUM;
      }
      
      if (product.loaded && product.valid && product.state === self.APPROVED && product.transaction != null) {
        product.finish();
      }
    });
    this.store.when(productId).registered((product: IAPProduct) => {
      IAPController.userType = IAPController.USER_TYPE_PREMIUM;
      console.log(`registered ${product.owned}`);
    });
    this.store.when(productId).owned((product: IAPProduct) => {
      console.log(`owned ${product.owned}`);
      IAPController.userType = IAPController.USER_TYPE_PREMIUM;
      product.finish();
    });
    this.store.when(productId).approved((product: IAPProduct) => {
      IAPController.userType = IAPController.USER_TYPE_PREMIUM;
      console.log('approved', product);
      product.finish();
    });
    this.store.when(productId).refunded((product: IAPProduct) => {
      IAPController.userType = IAPController.USER_TYPE_FREE;
      console.log('refunded', product);
    });
    this.store.when(productId).expired((product: IAPProduct) => {
      IAPController.userType = IAPController.USER_TYPE_FREE;
      console.log('expired', product);
    });
  }

}


