import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { MenuController } from '../../app/menu.controller';
import { QuizPage } from '../quiz/quiz';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage extends MenuController {
  nav: NavController;
  categoryQuizes: Array<{title: string, name: string, icon?: string, color?: string, component: any}>;
  freeQuizes: Array<{title: string, name: string, icon?: string, color?: string, component: any}>;
  
  constructor(public navCtrl: NavController) {
    
    super();
    this.nav = navCtrl;
    this.categoryQuizes = [
      { title: 'QUIZ_TORAX', name: 'torax', icon: this.getIconByUserType(), color: this.getColorByUserType(), component: QuizPage },
      { title: 'QUIZ_ABDOMEN', name: 'abdome', icon: this.getIconByUserType(), color: this.getColorByUserType(), component: QuizPage },
      { title: 'QUIZ_PELVE_PERINEO', name: 'pelve', icon: this.getIconByUserType(), color: this.getColorByUserType(), component: QuizPage },
      { title: 'QUIZ_DORSO', name: 'dorso', icon: this.getIconByUserType(), color: this.getColorByUserType(), component: QuizPage },
      { title: 'QUIZ_MEMBRO_INFERIOR', name: 'inferior', icon: this.getIconByUserType(), color: this.getColorByUserType(), component: QuizPage },
      { title: 'QUIZ_MEMBRO_SUPERIOR', name: 'superior', icon: this.getIconByUserType(), color: this.getColorByUserType(), component: QuizPage },
      { title: 'QUIZ_CABECA', name: 'cabeca', icon: this.getIconByUserType(), color: this.getColorByUserType(), component: QuizPage },
      { title: 'QUIZ_PESCOCO', name: 'pescoco', icon: this.getIconByUserType(), color: this.getColorByUserType(), component: QuizPage },
      { title: 'QUIZ_NERVOS_CARNIANOS', name: 'nervos', icon: this.getIconByUserType(), color: this.getColorByUserType(), component: QuizPage },
    ];

    this.freeQuizes = [
      { title: 'QUIZ_MIX_1', name: 'mix-1', icon: MenuController.UNLOCK_ICON, color: MenuController.UNLOCK_COLOR, component: QuizPage },
      { title: 'QUIZ_MIX_2', name: 'mix-2', icon: MenuController.UNLOCK_ICON, color: MenuController.UNLOCK_COLOR, component: QuizPage }
    ];

  }

  openPage(page) {
    this.openPageWithNav(page, this.nav);
  }

}



